@import './../../../../../src/styles/color.scss';

$unclicked-color: rgb(0, 0, 0);
.accordion-item-header {
  display: flex;
  align-items: center;
}

.treerow {
  width: 100vw;
}

.checked {
  font-weight: bold;
  border-radius: 100px;
  // color:  #{$baseColor}
}

.clicked {
  font-weight: bold;
  border-radius: 100px;
  // color:  #{$baseColor} !important;
}

.base {
  color: #{$unclicked-color};
}

.example-list {
  overflow: hidden;
}

.example-box {
  display: flex; //flex 친구들 필수!
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  // box-sizing: border-box;
  cursor: move;
  // font-size: 14px;
}

